import React, { useState, useEffect, useMemo } from "react";
import { useQuery } from "@tanstack/react-query";
import Page from "@/components/Page";
import Header from "@/components/Header";
import ErrorBoundary from "@/components/ErrorBoundary";
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from "@/components/ui/card";
import {
  PhoneCall,
  Clock,
  Users,
  Activity,
  CheckCircle,
  XCircle,
  AlertCircle,
  Mic,
  Loader2,
  RefreshCw,
} from "lucide-react";
import { Button } from "@/components/ui/button";

// Import API services
import {
  getDashboardData,
  clearDashboardCache,
  getAgentsFromDashboard,
  getCallsFromDashboard,
  getCallStatsFromDashboard,
  getModelStatsFromDashboard,
  getVoiceStatsFromDashboard,
  getPhoneNumbersFromDashboard
} from "@/api/services/dashboard/dashboardService";
import { useAuth } from "@/context/AuthContext";

// Modern chart components
import ModernBarChart from "@/components/dashboard/ModernBarChart";
import ModernLineChart from "@/components/dashboard/ModernLineChart";
import ModernPieChart from "@/components/dashboard/ModernPieChart";
import ModernProgressBar from "@/components/dashboard/ModernProgressBar";
import ModernStatCard from "@/components/dashboard/ModernStatCard";
import ModernInsightCard from "@/components/dashboard/ModernInsightCard";
import { DashboardSkeleton, ProgressiveLoader } from "@/components/dashboard/DashboardSkeleton";

// Dynamic dashboard data will be generated from API response inside the component

const Dashboard = () => {
  const { user } = useAuth();
  const [loadingStage, setLoadingStage] = useState(0);

  // Single optimized API call for all dashboard data
  const { data: dashboardResponse, isLoading, error, refetch } = useQuery({
    queryKey: ["dashboard", "comprehensive", user?.id],
    queryFn: async () => {
      setLoadingStage(0);
      const result = await getDashboardData();
      setLoadingStage(5); // Final stage
      return result;
    },
    retry: 2,
    enabled: !!user?.id,
    staleTime: 2 * 60 * 1000, // 2 minutes
    gcTime: 5 * 60 * 1000, // 5 minutes
    placeholderData: (previousData) => previousData,
  });

  // Simulate progressive loading stages
  useEffect(() => {
    if (isLoading) {
      const stages = [0, 1, 2, 3, 4];
      let currentStage = 0;

      const interval = setInterval(() => {
        if (currentStage < stages.length - 1) {
          currentStage++;
          setLoadingStage(currentStage);
        } else {
          clearInterval(interval);
        }
      }, 500); // Update stage every 500ms

      return () => clearInterval(interval);
    }
  }, [isLoading]);

  // Extract data from the comprehensive response
  const dashboardData = dashboardResponse?.data;
  const agentsData = dashboardData ? getAgentsFromDashboard(dashboardData) : null;
  const callsData = dashboardData ? getCallsFromDashboard(dashboardData) : null;
  const callStatsData = dashboardData ? getCallStatsFromDashboard(dashboardData) : null;
  const modelStatsData = dashboardData ? getModelStatsFromDashboard(dashboardData) : null;
  const voiceStatsData = dashboardData ? getVoiceStatsFromDashboard(dashboardData) : null;
  const phoneNumbersData = dashboardData ? getPhoneNumbersFromDashboard(dashboardData) : null;

  // Create dynamic chart data from API response
  const callVolumeData = useMemo(() => {
    if (callsData?.data && Array.isArray(callsData.data)) {
      // Group calls by day of week
      const callsByDay = callsData.data.reduce((acc: any, call: any) => {
        const date = new Date(call.createdAt || call.startedAt || Date.now());
        const dayName = date.toLocaleDateString('en-US', { weekday: 'short' });
        acc[dayName] = (acc[dayName] || 0) + 1;
        return acc;
      }, {});

      return ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'].map(day => ({
        name: day,
        calls: callsByDay[day] || 0
      }));
    }
    // Fallback to mock data if no real data
    return [
      { name: "Mon", calls: 120 },
      { name: "Tue", calls: 145 },
      { name: "Wed", calls: 160 },
      { name: "Thu", calls: 170 },
      { name: "Fri", calls: 190 },
      { name: "Sat", calls: 80 },
      { name: "Sun", calls: 60 },
    ];
  }, [callsData]);

  const callDurationData = useMemo(() => {
    if (callsData?.data && Array.isArray(callsData.data)) {
      // Group calls by day and calculate average duration
      const durationsByDay = callsData.data.reduce((acc: any, call: any) => {
        const date = new Date(call.createdAt || call.startedAt || Date.now());
        const dayName = date.toLocaleDateString('en-US', { weekday: 'short' });
        if (!acc[dayName]) acc[dayName] = { total: 0, count: 0 };
        acc[dayName].total += call.duration || 0;
        acc[dayName].count += 1;
        return acc;
      }, {});

      return ['Mon', 'Tue', 'Wed', 'Thu', 'Fri', 'Sat', 'Sun'].map(day => ({
        name: day,
        avgDuration: durationsByDay[day] ? parseFloat((durationsByDay[day].total / durationsByDay[day].count / 60).toFixed(1)) : 0
      }));
    }
    // Fallback to mock data
    return [
      { name: "Mon", avgDuration: 3.2 },
      { name: "Tue", avgDuration: 3.5 },
      { name: "Wed", avgDuration: 3.8 },
      { name: "Thu", avgDuration: 3.6 },
      { name: "Fri", avgDuration: 3.9 },
      { name: "Sat", avgDuration: 2.8 },
      { name: "Sun", avgDuration: 2.5 },
    ];
  }, [callsData]);

  const callOutcomeData = useMemo(() => {
    if (callsData?.data && Array.isArray(callsData.data)) {
      const outcomes = callsData.data.reduce((acc: any, call: any) => {
        const status = call.status || 'unknown';
        if (status === 'ended' || status === 'completed') {
          acc.successful = (acc.successful || 0) + 1;
        } else if (status === 'failed' || status === 'error') {
          acc.failed = (acc.failed || 0) + 1;
        } else {
          acc.other = (acc.other || 0) + 1;
        }
        return acc;
      }, {});

      const total = callsData.data.length;
      return [
        { name: "Successful", value: Math.round((outcomes.successful || 0) / total * 100) },
        { name: "Failed", value: Math.round((outcomes.failed || 0) / total * 100) },
        { name: "Other", value: Math.round((outcomes.other || 0) / total * 100) },
      ];
    }
    // Fallback to mock data
    return [
      { name: "Successful", value: 65 },
      { name: "Failed", value: 15 },
      { name: "Abandoned", value: 20 },
    ];
  }, [callsData]);

  const agentPerformanceData = useMemo(() => {
    if (agentsData?.data?.assistants && Array.isArray(agentsData.data.assistants)) {
      return agentsData.data.assistants.slice(0, 4).map((agent: any, index: number) => ({
        name: agent.name || `Agent ${index + 1}`,
        successRate: Math.floor(Math.random() * 30) + 70, // Mock success rate for now
        avgCallTime: (Math.random() * 2 + 2).toFixed(1), // Mock avg call time
        callsHandled: Math.floor(Math.random() * 200) + 100 // Mock calls handled
      }));
    }
    // Fallback to mock data
    return [
      { name: "Sales Agent", successRate: 78, avgCallTime: 3.5, callsHandled: 245 },
      { name: "Support Agent", successRate: 82, avgCallTime: 4.2, callsHandled: 320 },
      { name: "Appointment Agent", successRate: 75, avgCallTime: 2.8, callsHandled: 180 },
      { name: "Survey Agent", successRate: 88, avgCallTime: 3.1, callsHandled: 210 },
    ];
  }, [agentsData]);

  const recentCalls = useMemo(() => {
    if (callsData?.data && Array.isArray(callsData.data)) {
      return callsData.data.slice(0, 5).map((call: any, index: number) => ({
        id: call.id || index,
        agent: call.assistantId || `Agent ${index + 1}`,
        duration: call.duration ? `${Math.floor(call.duration / 60)}:${(call.duration % 60).toString().padStart(2, '0')}` : "0:00",
        status: call.status === 'ended' ? 'Completed' : (call.status || 'Unknown'),
        outcome: call.status === 'ended' ? 'Successful' : 'Failed',
        time: call.createdAt ? new Date(call.createdAt).toLocaleTimeString('en-US', {
          hour: 'numeric',
          minute: '2-digit',
          hour12: true
        }) : 'Unknown'
      }));
    }
    // Fallback to mock data
    return [
      { id: 1, agent: "Sales Agent", duration: "3:45", status: "Completed", outcome: "Successful", time: "10:30 AM" },
      { id: 2, agent: "Support Agent", duration: "5:12", status: "Completed", outcome: "Successful", time: "11:15 AM" },
      { id: 3, agent: "Appointment Agent", duration: "2:30", status: "Completed", outcome: "Failed", time: "12:05 PM" },
      { id: 4, agent: "Survey Agent", duration: "3:18", status: "Completed", outcome: "Successful", time: "1:30 PM" },
      { id: 5, agent: "Sales Agent", duration: "4:05", status: "Completed", outcome: "Successful", time: "2:15 PM" },
    ];
  }, [callsData]);

  // Calculate real metrics from dashboard summary or fallback to individual data
  const summary = dashboardData?.summary;
  const totalAgents = summary?.totalAgents || agentsData?.data?.assistants?.length || 0;
  const totalCalls = summary?.totalCalls || callStatsData?.data?.totalCalls || callsData?.length || 0;
  const avgDuration = summary?.avgCallDuration || callStatsData?.data?.averageDuration || 0;
  const successRate = summary?.successRate || (
    callStatsData?.data?.successfulCalls && callStatsData?.data?.totalCalls
      ? ((callStatsData.data.successfulCalls / callStatsData.data.totalCalls) * 100).toFixed(1) + "%"
      : "0%"
  );

  // Dynamic trend calculations
  const callsTrend = useMemo(() => {
    if (callVolumeData && callVolumeData.length > 1) {
      const recent = callVolumeData.slice(-2);
      const change = recent[1].calls - recent[0].calls;
      const percentage = recent[0].calls > 0 ? Math.abs((change / recent[0].calls) * 100).toFixed(1) : "0";
      return {
        value: `${percentage}%`,
        direction: change >= 0 ? "up" as const : "down" as const,
        label: "from yesterday"
      };
    }
    return { value: "12%", direction: "up" as const, label: "from last week" };
  }, [callVolumeData]);

  const durationTrend = useMemo(() => {
    if (callDurationData && callDurationData.length > 1) {
      const recent = callDurationData.slice(-2);
      const current = typeof recent[1].avgDuration === 'string' ? parseFloat(recent[1].avgDuration) : recent[1].avgDuration;
      const previous = typeof recent[0].avgDuration === 'string' ? parseFloat(recent[0].avgDuration) : recent[0].avgDuration;
      const change = current - previous;
      const percentage = previous > 0 ? Math.abs((change / previous) * 100).toFixed(1) : "0";
      return {
        value: `${percentage}%`,
        direction: change >= 0 ? "up" as const : "down" as const,
        label: "from yesterday"
      };
    }
    return { value: "5%", direction: "up" as const, label: "from last week" };
  }, [callDurationData]);

  const agentsTrend = useMemo(() => {
    // For agents, we can show how many are new
    const newAgents = Math.max(0, totalAgents - Math.floor(totalAgents * 0.9));
    return {
      value: newAgents > 0 ? `${newAgents} new` : "stable",
      direction: newAgents > 0 ? "up" as const : "neutral" as const,
      label: "this week"
    };
  }, [totalAgents]);

  const successTrend = useMemo(() => {
    if (callOutcomeData && callOutcomeData.length > 0) {
      const successfulPercentage = callOutcomeData.find(item => item.name === "Successful")?.value || 0;
      const change = successfulPercentage - 62; // Compare to baseline
      return {
        value: `${Math.abs(change).toFixed(1)}%`,
        direction: change >= 0 ? "up" as const : "down" as const,
        label: "from last week"
      };
    }
    return { value: "3%", direction: "down" as const, label: "from last week" };
  }, [callOutcomeData]);

  // Dynamic performance metrics
  const performanceMetrics = useMemo(() => {
    const baseMetrics = {
      responseLatency: { value: 15, status: "success" as 'success' | 'warning' | 'error' | 'info', text: "Excellent (450ms)" },
      speechAccuracy: { value: 92, status: "success" as 'success' | 'warning' | 'error' | 'info', text: "Good (92%)" },
      apiReliability: { value: 98.5, status: "warning" as 'success' | 'warning' | 'error' | 'info', text: "Good (98.5%)" },
      completionRate: { value: 89, status: "success" as 'success' | 'warning' | 'error' | 'info', text: "Good (89%)" }
    };

    // If we have real data, calculate actual metrics
    if (callsData?.data && Array.isArray(callsData.data)) {
      const completedCalls = callsData.data.filter(call => call.status === 'ended' || call.status === 'completed');
      const completionRate = callsData.data.length > 0 ? (completedCalls.length / callsData.data.length) * 100 : 0;

      const dynamicStatus: 'success' | 'warning' | 'error' = completionRate > 85 ? "success" : completionRate > 70 ? "warning" : "error";

      baseMetrics.completionRate = {
        value: Math.round(completionRate),
        status: dynamicStatus,
        text: `${completionRate > 85 ? "Excellent" : completionRate > 70 ? "Good" : "Poor"} (${Math.round(completionRate)}%)`
      };
    }

    return baseMetrics;
  }, [callsData]);

  const insightMetrics = useMemo(() => {
    const baseInsights = {
      wordsPerMinute: "145 WPM",
      interruptionRate: "3.2%",
      sentimentScore: "+0.68"
    };

    // Calculate dynamic insights if we have call data
    if (callsData?.data && Array.isArray(callsData.data)) {
      const avgDurationMinutes = callsData.data.reduce((sum, call) => sum + (call.duration || 0), 0) / callsData.data.length / 60;
      const estimatedWPM = Math.round(avgDurationMinutes * 150); // Estimate based on average speaking rate

      baseInsights.wordsPerMinute = `${estimatedWPM} WPM`;
      baseInsights.interruptionRate = `${(Math.random() * 5 + 1).toFixed(1)}%`; // Mock for now
      baseInsights.sentimentScore = `+${(Math.random() * 0.5 + 0.4).toFixed(2)}`; // Mock positive sentiment
    }

    return baseInsights;
  }, [callsData]);

  // Update loading state to use the single API call
  const isDashboardLoading = isLoading;

  if (isDashboardLoading) {
    return (
      <Page>
        <Header
          title="Voice AI Dashboard"
          buttonText="Loading..."
          action={() => {}}
          filterByName={false}
        />
        <ProgressiveLoader stage={loadingStage} />
      </Page>
    );
  }

  // Handle error state
  if (error) {
    return (
      <Page>
        <Header
          title="Voice AI Dashboard"
          buttonText="New Campaign"
          action={() => console.log("Adding new campaign...")}
          filterByName={true}
        />
        <div className="p-6">
          <Card className="card">
            <CardContent className="p-6 text-center">
              <p className="text-muted-foreground mb-4">
                Failed to load dashboard data. Please try again.
              </p>
              <Button onClick={() => refetch()}>
                <RefreshCw className="h-4 w-4 mr-2" />
                Retry
              </Button>
            </CardContent>
          </Card>
        </div>
      </Page>
    );
  }

  return (
    <Page>
      <Header
        title={`Voice AI Dashboard ${dashboardResponse?.cached ? '(Cached)' : ''}`}
        buttonText="Refresh Data"
        action={() => refetch()}
        filterByName={true}
      />



      <div className="p-6 space-y-6">
        {/* Key Metrics */}
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
          <ModernStatCard
            title="Total Calls"
            value={totalCalls.toLocaleString()}
            icon={<PhoneCall className="h-5 w-5" />}
            trend={callsTrend}
            animationDelay={100}
          />

          <ModernStatCard
            title="Avg. Call Duration"
            value={avgDuration > 0 ? `${Math.floor(avgDuration / 60)}:${(avgDuration % 60).toString().padStart(2, '0')}` : "0:00"}
            icon={<Clock className="h-5 w-5" />}
            trend={durationTrend}
            iconColor="text-chart-2"
            iconBgColor="bg-gradient-to-br from-chart-2/20 to-chart-2/10"
            animationDelay={200}
          />

          <ModernStatCard
            title="Active Agents"
            value={totalAgents.toString()}
            icon={<Users className="h-5 w-5" />}
            trend={agentsTrend}
            iconColor="text-chart-3"
            iconBgColor="bg-gradient-to-br from-chart-3/20 to-chart-3/10"
            animationDelay={300}
          />

          <ModernStatCard
            title="Success Rate"
            value={successRate.toString().replace('%', '') + '%'}
            icon={<Activity className="h-5 w-5" />}
            trend={successTrend}
            iconColor="text-chart-4"
            iconBgColor="bg-gradient-to-br from-chart-4/20 to-chart-4/10"
            animationDelay={400}
          />
        </div>

        {/* Charts Row 1 */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <Card className="card overflow-hidden">
            <CardHeader className="border-b border-border/50">
              <CardTitle className="text-lg font-semibold jakarta">
                Call Volume
              </CardTitle>
              <CardDescription>Number of calls per day</CardDescription>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="h-[300px]">
                <ModernBarChart
                  data={callVolumeData}
                  dataKeys={[
                    {
                      key: "calls",
                      name: "Number of Calls",
                      color: "hsl(var(--primary))"
                    }
                  ]}
                  xAxisDataKey="name"
                  height={300}
                  barRadius={[4, 4, 0, 0]}
                  tooltipFormatter={(value) => `${value} calls`}
                  tooltipLabelFormatter={(label) => `Day: ${label}`}
                  animationDuration={800}
                />
              </div>
            </CardContent>
          </Card>

          <Card className="card overflow-hidden">
            <CardHeader className="border-b border-border/50">
              <CardTitle className="text-lg font-semibold jakarta">
                Average Call Duration
              </CardTitle>
              <CardDescription>Average call time in minutes</CardDescription>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="h-[300px]">
                <ModernLineChart
                  data={callDurationData}
                  dataKeys={[
                    {
                      key: "avgDuration",
                      name: "Average Duration",
                      color: "hsl(var(--chart-2))",
                      strokeWidth: 2,
                      dotRadius: 4,
                      activeDotRadius: 6
                    }
                  ]}
                  xAxisDataKey="name"
                  height={300}
                  tooltipFormatter={(value) => `${value} min`}
                  tooltipLabelFormatter={(label) => `Day: ${label}`}
                  animationDuration={1200}
                />
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Charts Row 2 */}
        <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
          <Card className="card overflow-hidden">
            <CardHeader className="border-b border-border/50">
              <CardTitle className="text-lg font-semibold jakarta">
                Call Outcomes
              </CardTitle>
              <CardDescription>Distribution of call results</CardDescription>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="h-[300px] flex items-center justify-center">
                <ModernPieChart
                  data={callOutcomeData}
                  dataKey="value"
                  nameKey="name"
                  colors={["#4ade80", "#f87171", "#fbbf24"]}
                  height={300}
                  outerRadius={90}
                  innerRadius={0}
                  label={({ name, percent }) => `${name}: ${(percent * 100).toFixed(0)}%`}
                  tooltipFormatter={(value) => `${value} calls`}
                  animationDuration={1000}
                />
              </div>
            </CardContent>
          </Card>

          <Card className="col-span-1 lg:col-span-2 card overflow-hidden">
            <CardHeader className="border-b border-border/50">
              <CardTitle className="text-lg font-semibold jakarta">
                Agent Performance
              </CardTitle>
              <CardDescription>Success rates by agent type</CardDescription>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="h-[300px]">
                <ModernBarChart
                  data={agentPerformanceData}
                  dataKeys={[
                    {
                      key: "successRate",
                      name: "Success Rate (%)",
                      color: "hsl(var(--chart-1))"
                    },
                    {
                      key: "avgCallTime",
                      name: "Avg Call Time (min)",
                      color: "hsl(var(--chart-3))"
                    }
                  ]}
                  xAxisDataKey="name"
                  height={300}
                  barRadius={[4, 4, 0, 0]}
                  tooltipFormatter={(value, name) =>
                    name === "Success Rate (%)" ? `${value}%` : `${value} min`
                  }
                  tooltipLabelFormatter={(label) => `Agent: ${label}`}
                  animationDuration={1000}
                />
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Voice Agent Health Monitoring */}
        <div className="grid grid-cols-1 lg:grid-cols-2 gap-6">
          <Card className="card overflow-hidden">
            <CardHeader className="border-b border-border/50">
              <CardTitle className="text-lg font-semibold jakarta">
                Voice Agent Health
              </CardTitle>
              <CardDescription>Real-time performance metrics</CardDescription>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="space-y-5">
                <ModernProgressBar
                  label="Response Latency"
                  value={performanceMetrics.responseLatency.value}
                  maxValue={100}
                  status={performanceMetrics.responseLatency.status}
                  statusText={performanceMetrics.responseLatency.text}
                  height={8}
                  animationDuration={800}
                />

                <ModernProgressBar
                  label="Speech Recognition Accuracy"
                  value={performanceMetrics.speechAccuracy.value}
                  maxValue={100}
                  status={performanceMetrics.speechAccuracy.status}
                  statusText={performanceMetrics.speechAccuracy.text}
                  height={8}
                  animationDuration={1000}
                />

                <ModernProgressBar
                  label="Voice Naturalness"
                  value={95}
                  maxValue={100}
                  status="success"
                  statusText="Excellent (95%)"
                  height={8}
                  animationDuration={1200}
                />

                <ModernProgressBar
                  label="API Reliability"
                  value={performanceMetrics.apiReliability.value}
                  maxValue={100}
                  status={performanceMetrics.apiReliability.status}
                  statusText={performanceMetrics.apiReliability.text}
                  height={8}
                  animationDuration={1400}
                />

                <ModernProgressBar
                  label="Call Completion Rate"
                  value={performanceMetrics.completionRate.value}
                  maxValue={100}
                  status={performanceMetrics.completionRate.status}
                  statusText={performanceMetrics.completionRate.text}
                  height={8}
                  animationDuration={1600}
                />
              </div>
            </CardContent>
          </Card>

          <Card className="card overflow-hidden">
            <CardHeader className="border-b border-border/50">
              <CardTitle className="text-lg font-semibold jakarta">
                Voice Agent Insights
              </CardTitle>
              <CardDescription>Key performance indicators</CardDescription>
            </CardHeader>
            <CardContent className="pt-6">
              <div className="space-y-4">
                <ModernInsightCard
                  title="Average Words Per Minute"
                  value={insightMetrics.wordsPerMinute}
                  icon={<Mic className="h-6 w-6" />}
                  iconColor="text-primary"
                  iconBgColor="bg-gradient-to-br from-primary/20 to-primary/10"
                  animationDelay={100}
                />

                <ModernInsightCard
                  title="Interruption Rate"
                  value={insightMetrics.interruptionRate}
                  icon={<Activity className="h-6 w-6" />}
                  iconColor="text-chart-3"
                  iconBgColor="bg-gradient-to-br from-chart-3/20 to-chart-3/10"
                  animationDelay={300}
                />

                <ModernInsightCard
                  title="Sentiment Score"
                  value={insightMetrics.sentimentScore}
                  icon={<CheckCircle className="h-6 w-6" />}
                  iconColor="text-chart-4"
                  iconBgColor="bg-gradient-to-br from-chart-4/20 to-chart-4/10"
                  animationDelay={500}
                />
              </div>
            </CardContent>
          </Card>
        </div>

        {/* Recent Calls Table */}
        <Card className="card overflow-hidden">
          <CardHeader className="border-b border-border/50">
            <CardTitle className="text-lg font-semibold jakarta">
              Recent Calls
            </CardTitle>
            <CardDescription>Latest voice agent interactions</CardDescription>
          </CardHeader>
          <CardContent className="pt-6">
            <div className="overflow-x-auto">
              <table className="w-full text-sm">
                <thead>
                  <tr className="border-b border-border/50">
                    <th className="text-left py-3 px-4 font-medium text-muted-foreground">
                      ID
                    </th>
                    <th className="text-left py-3 px-4 font-medium text-muted-foreground">
                      Agent
                    </th>
                    <th className="text-left py-3 px-4 font-medium text-muted-foreground">
                      Duration
                    </th>
                    <th className="text-left py-3 px-4 font-medium text-muted-foreground">
                      Status
                    </th>
                    <th className="text-left py-3 px-4 font-medium text-muted-foreground">
                      Outcome
                    </th>
                    <th className="text-left py-3 px-4 font-medium text-muted-foreground">
                      Time
                    </th>
                  </tr>
                </thead>
                <tbody>
                  {recentCalls.map((call) => (
                    <tr
                      key={call.id}
                      className="border-b border-border/30 hover:bg-secondary/40 transition-colors"
                    >
                      <td className="py-3 px-4 jakarta">#{call.id}</td>
                      <td className="py-3 px-4 flex items-center">
                        <div className="h-7 w-7 rounded-full bg-gradient-to-br from-primary/20 to-primary/10 flex items-center justify-center mr-2">
                          <Mic className="h-3.5 w-3.5 text-primary" />
                        </div>
                        <span className="jakarta">{call.agent}</span>
                      </td>
                      <td className="py-3 px-4 jakarta">{call.duration}</td>
                      <td className="py-3 px-4">
                        <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-green-100/80 text-green-800 jakarta">
                          <CheckCircle className="h-3 w-3 mr-1" />
                          {call.status}
                        </span>
                      </td>
                      <td className="py-3 px-4">
                        {call.outcome === "Successful" && (
                          <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-green-100/80 text-green-800 jakarta">
                            <CheckCircle className="h-3 w-3 mr-1" />
                            {call.outcome}
                          </span>
                        )}
                        {call.outcome === "Failed" && (
                          <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-red-100/80 text-red-800 jakarta">
                            <XCircle className="h-3 w-3 mr-1" />
                            {call.outcome}
                          </span>
                        )}
                        {call.outcome === "Abandoned" && (
                          <span className="inline-flex items-center px-2.5 py-1 rounded-full text-xs font-medium bg-yellow-100/80 text-yellow-800 jakarta">
                            <AlertCircle className="h-3 w-3 mr-1" />
                            {call.outcome}
                          </span>
                        )}
                      </td>
                      <td className="py-3 px-4 jakarta">{call.time}</td>
                    </tr>
                  ))}
                </tbody>
              </table>
            </div>
          </CardContent>
        </Card>
      </div>
    </Page>
  );
};

export default Dashboard;
