// Progressive loading component
export const ProgressiveLoader = ({ 
  stage, 
  stages = [
    "Connecting to services...",
    "Fetching agents data...",
    "Loading call statistics...",
    "Retrieving phone numbers...",
    "Calculating analytics...",
    "Finalizing dashboard..."
  ]
}: { 
  stage: number; 
  stages?: string[] 
}) => (
  <div className="p-6">
    <Card className="card">
      <CardContent className="p-8 text-center">
        <div className="space-y-6">
          <div className="mx-auto w-16 h-16 relative">
            <div className="absolute inset-0 rounded-full border-4 border-muted"></div>
            <div 
              className="absolute inset-0 rounded-full border-4 border-primary border-t-transparent animate-spin"
              style={{
                animationDuration: '1s'
              }}
            ></div>
          </div>
          
          <div className="space-y-3">
            <h3 className="text-lg font-semibold">Loading Dashboard</h3>
            <p className="text-muted-foreground">
              {stages[Math.min(stage, stages.length - 1)]}
            </p>
            
            {/* Progress bar */}
            <div className="w-full bg-muted rounded-full h-2 max-w-xs mx-auto">
              <div 
                className="bg-primary h-2 rounded-full transition-all duration-500 ease-out"
                style={{ 
                  width: `${Math.min((stage + 1) / stages.length * 100, 100)}%` 
                }}
              ></div>
            </div>
            
            <p className="text-sm text-muted-foreground">
              Step {Math.min(stage + 1, stages.length)} of {stages.length}
            </p>
          </div>
        </div>
      </CardContent>
    </Card>
  </div>
);

export default DashboardSkeleton;
